$square-size: 96px;
$square-border-size: 1px;
$square-gap-size: 1px;
$days-in-row: 7;
$square-size-full: calc($square-size + $square-gap-size + $square-border-size * 2);
$square-color: #eee;
$calendar-font-family: Arial, sans-serif;
$days: (
  'mon': 0,
  'tue': 1,
  'wed': 2,
  'thu': 3,
  'fri': 4,
  'sat': 5,
  'sun': 6,
);
$calendar-width: calc($square-size-full * $days-in-row);